import clsx from 'clsx'
import s from './PopularModel.module.scss'
import pt from 'prop-types'

const PopularModel = (props) => {
  return (
    <li className={clsx(s['PopularModel'])}>
      
      <div className={clsx(s['img-container'])}>
        <img src={props.imgRef} />
      </div>

      <div className={clsx(s['right'])}>

        <div className={clsx(s['name'])}>
          Iphone 12 Max
        </div>

        <ul className={clsx(s['colors'])}>
          <li className={clsx(s['color'])}></li>
          <li className={clsx(s['color'])}></li>
          <li className={clsx(s['color'])}></li>
        </ul>

        <div className={clsx(s['price'])}>
          <div className={clsx(s['real-price'])}>
            $586
          </div>
          <div className={clsx(s['price-deleted'])}>
            $46.25
          </div>
        </div>

      </div>

      <div className={clsx(s['sale'])}>
        Sale
      </div>

    </li>
  )
}

PopularModel.propTypes = {
  imgRef: pt.string.isRequired
}

export default PopularModel
