import clsx from 'clsx'
import Product from 'components/Product/Product'
import s from './Products.module.scss'

import p1 from 'assets/images/products/p-1.png'
import p2 from 'assets/images/products/p-2.png'
import p3 from 'assets/images/products/p-3.png'
import p4 from 'assets/images/products/p-4.png'
import p5 from 'assets/images/products/p-5.png'
import p6 from 'assets/images/products/p-6.png'
import p7 from 'assets/images/products/p-7.png'
import p8 from 'assets/images/products/p-8.png'
import Pagination from 'components/Pagination/Pagination'

const Products = () => {
  const products = [
    {
      name: 'Airpods 4 PRO',
      availability: 'available',
      imgRef: p1,
      colors: ['rgba(235, 58, 58, 1)', 'rgba(0, 0, 0, 1)', 'rgba(230, 231, 235, 1)'],
      price: '999$'
    },
    {
      name: 'Airpods 4 PRO',
      availability: 'available',
      imgRef: p2,
      colors: ['rgba(113, 113, 113, 1)', 'rgba(0, 0, 0, 1)'],
      price: '864$'
    },
    {
      name: 'Airpods 4 PRO',
      availability: 'not-available',
      imgRef: p3,
      colors: ['rgba(235, 58, 58, 1)', 'rgba(98, 222, 203, 1)', 'rgba(250, 193, 2, 1)'],
      price: '744$'
    },
    {
      name: 'Airpods 4 PRO',
      availability: 'in-delivery',
      imgRef: p4,
      colors: ['rgba(232, 232, 232, 1)', 'rgba(0, 0, 0, 1)'],
      price: '299$'
    },
    {
      name: 'Airpods 4 PRO',
      availability: 'in-delivery',
      imgRef: p5,
      colors: ['rgba(232, 232, 232, 1)', 'rgba(0, 0, 0, 1)'],
      price: '299$'
    },
    {
      name: 'Airpods 4 PRO',
      availability: 'in-delivery',
      imgRef: p6,
      colors: ['rgba(232, 232, 232, 1)', 'rgba(0, 0, 0, 1)'],
      price: '299$'
    },
    {
      name: 'Airpods 4 PRO',
      availability: 'in-delivery',
      imgRef: p7,
      colors: ['rgba(232, 232, 232, 1)', 'rgba(0, 0, 0, 1)'],
      price: '299$'
    },
    {
      name: 'Airpods 4 PRO',
      availability: 'in-delivery',
      imgRef: p8,
      colors: ['rgba(232, 232, 232, 1)', 'rgba(0, 0, 0, 1)'],
      price: '299$'
    },
  ]

  const renderedProducts = products.map(p => (
    <Product
      name={p.name}
      availability={p.availability}
      imgRef={p.imgRef}
      colors={p.colors}
      price={p.price}
    />
  ))

  return (
    <div className={clsx(s['Products'])}>
      
      <div className={clsx(s['title'])}>
        Products
      </div>

      <ul className={clsx(s['list'])}>
        {renderedProducts}
      </ul>

      <Pagination />

    </div>
  )
}

export default Products
