import clsx from 'clsx'
import PopularModel from 'components/PopularModel/PopularModel'
import s from './PopularModels.module.scss'
import Pagination from 'components/Pagination/Pagination'
import popular_1 from 'assets/images/popular/popular-1.png'
import popular_2 from 'assets/images/popular/popular-2.png'
import popular_3 from 'assets/images/popular/popular-3.png'

const PopularModels = () => {
  return (
    <div className={clsx(s['PopularModels'])}>
      
      <div className={clsx(s['title'])}>
        Popular models
      </div>

      <ul className={clsx(s['list'])}>
        <PopularModel
          imgRef={popular_1}
        />
        <PopularModel
          imgRef={popular_2}
        />
        <PopularModel
          imgRef={popular_3}
        />
      </ul>

      <Pagination />

    </div>
  )
}

export default PopularModels
