import clsx from 'clsx'
import s from './Footer.module.scss'
import telegramIcon from 'assets/icons/telegram-icon.png'
import instaIcon from 'assets/icons/insta-icon.png'
import googleIcon from 'assets/icons/google-icon.png'

const Footer = () => {
  return (
    <footer className={clsx(s['Footer'])}>
      
      <div className={clsx(s['section'])}>

        <div className={clsx(s['title'])}>Our products</div>

        <ul className={clsx(s['links'])}>
          <li className={clsx(s['link'])}>
            Macbook
          </li>
          <li className={clsx(s['link'])}>
            Iphone
          </li>
          <li className={clsx(s['link'])}>
            AirPods
          </li>
        </ul>

      </div>
      <div className={clsx(s['section'])}>

        <div className={clsx(s['title'])}>Main</div>

        <ul className={clsx(s['links'])}>
          <li className={clsx(s['link'])}>
            About Macbook
          </li>
          <li className={clsx(s['link'])}>
            About Iphone
          </li>
          <li className={clsx(s['link'])}>
            About AirPods
          </li>
        </ul>

      </div>
      <div className={clsx(s['section'])}>

        <div className={clsx(s['title'])}>Contact</div>

        <ul className={clsx(s['links'])}>
          <li className={clsx(s['link'])}>
            +998 91 400-74-39
          </li>
          <li className={clsx(s['link'])}>
            {`09:00 â€” 20:00
            Monday - Sunday`}
          </li>
          <li className={clsx(s['link'])}>
            {`Uzbekistan, Tashkent,
            Chilonzor street 25`}
          </li>
        </ul>

      </div>
      <div className={clsx(s['section'])}>

        <div className={clsx(s['title'])}>Social Media</div>

        <ul className={clsx(s['links'], s['social-links'])}>
          <li className={clsx(s['link'])}>
            <img src={telegramIcon} />
          </li>
          <li className={clsx(s['link'])}>
            <img src={googleIcon} />
          </li>
          <li className={clsx(s['link'])}>
            <img src={instaIcon} />
          </li>
        </ul>

      </div>

      <div className={clsx(s['rights'])}>
        All Rights Preserved
      </div>

    </footer>
  )
}

export default Footer
