import clsx from 'clsx'
import { useState } from 'react'
import s from './Header.module.scss'
import { Divider, Drawer } from '@material-ui/core'
import { withStyles } from '@material-ui/styles'

const Header = () => {
  const [sideNavOpened, setSideNavOpened] = useState(false)


  return <>
    <header className={clsx(s['Header'])}>
    
      <div className={clsx(s['logo'])}>
        SmartTech.
      </div>

      <nav className={clsx(s['nav'])}>
        <ul className={clsx(s['nav-list'])}>
          <li className={clsx(s['nav-link'])}>
            Main
          </li>
          <li className={clsx(s['nav-link'])}>
            MacBook
          </li>
          <li className={clsx(s['nav-link'])}>
            IPhone
          </li>
          <li className={clsx(s['nav-link'], s['active'])}>
            AirPods
          </li>
          <li className={clsx(s['nav-link'])}>
            <span className={clsx(['material-icons', s['location-icon']])}>
              location_on
            </span>
            Location
          </li>
        </ul>
      </nav>

      <div className={clsx(s['search-panel'])}>
        <span className={clsx(['material-icons', s['search-icon']])}>
          search
        </span>
        <input
          type="text"
          placeholder="Search"
          className={clsx(s['input'])}
        />
      </div>

      <span
        onClick={() => setSideNavOpened(true)}
        className={clsx('material-icons', s['menu-icon'])}
      >
        menu
      </span>

      <StyledDrawer
        anchor={'left'}
        open={sideNavOpened}
        onClose={() => setSideNavOpened(false)}
      >
        <ul className={clsx(s['side-nav-list'])}>
          <li className={clsx(s['side-nav-link'])}>
            Main
          </li><Divider />
          <li className={clsx(s['side-nav-link'])}>
            Macbook
          </li><Divider />
          <li className={clsx(s['side-nav-link'])}>
            iPhone
          </li><Divider />
          <li className={clsx(s['side-nav-link'])}>
            AirPods
          </li><Divider />
          <li className={clsx(s['side-nav-link'])}>
            Location
          </li>
        </ul>
      </StyledDrawer>

    </header>
    <div className={clsx(s['seperator'])}></div>
  </>
}

const StyledDrawer = withStyles({
  paper: {
    paddingTop: '30px',
    width: '200px'
  }
})(Drawer)

export default Header
