import clsx from 'clsx'
import s from './Pagination.module.scss'

const Pagination = () => {
  return (
    <div className={clsx(s['Pagination'])}>
      <ul className={clsx(s['items'])}>
        <li className={clsx(s['item'])}>
          1
        </li>
        <li className={clsx(s['item'], s['selected'])}>
          2
        </li>
        <li className={clsx(s['item'])}>
          3
        </li>
      </ul>
    </div>
  )
}

export default Pagination
